import React, { useState, useEffect } from 'react';
import SubPageWrapper from '../SubPageWrapper';
import { IArticleShort } from '../../components/articleView';
import api from '../../api';
import Article from '../../components/article';

export default () => {
  const [notices, setNotices] = useState<IArticleShort[]>([]);
  useEffect(() => {
    api.get("/v1/notice")
      .then(({ data }) => setNotices(data))
  }, [])
  return (
    <SubPageWrapper navKey="support-notice">
      {notices.map((v, i) => (
        <Article key={i} end="support/notice" {...v} />
      ))}
    </SubPageWrapper>
  );
}