import React, { useState, useEffect } from 'react';
import SubPageWrapper from '../SubPageWrapper';
import { RouteComponentProps } from 'react-router-dom';
import ArticleView, { IArticle, IArticleShort } from "../../components/articleView";
import api from '../../api';

export default ({ match }: RouteComponentProps<{ id: string }>) => {
  const id = Number.parseInt(match.params.id);

  const [pre, setPre] = useState<IArticleShort | null>(null);
  const [post, setPost] = useState<IArticleShort | null>(null);

  const [article, setArticle] = useState<IArticle | null>({
    title: "",
    index: 1,
    date: "",
    content: "",
    attachments: [],
  });

  useEffect(() => {
    api.get(`/v1/notice/${id}`)
      .then(({ data }) => {
        setArticle(data);
      })
      .catch(() => setArticle(null))
    api.get(`v1/notice?id=${id - 1}`)
      .then(({ data }) => setPre(data))
      .catch(() => setPre(null))
    api.get(`v1/notice?id=${id + 1}`)
      .then(({ data }) => setPost(data))
      .catch(() => setPost(null))
  }, [id])
  return (
    <SubPageWrapper navKey="support-notice">
      {article ? <ArticleView {...article} end="support/notice" pre={pre} post={post} /> : "공지를 찾을 수 없습니다."}
    </SubPageWrapper>
  );
}