import React from 'react';
import ReactMarkdown from 'react-markdown';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

export interface IArticle {
  title: string;
  index: number;
  date: string;
  content: string;
  attachments: IAttachment[];
};

export interface IArticleShort {
  title: string;
  index: number;
}

interface IAttachment {
  fileName: string;
  link: string;
};

const Code = (props: any) => {
  return (
    <pre style={{ background: "#ff8", color: "#75751a", padding: 4, borderRadius: 4 }}>
      <code>
        {props.value}
      </code>
    </pre>
  )
}

const InlineCode = (props: any) => {
  return (
    <span style={{ background: "#ff8", color: "#75751a", padding: 4, borderRadius: 4 }}>
      {props.value}
    </span>
  )
}

const Image = (props: any) => {
  return (
    <img {...props} alt={props.alt} style={{ maxWidth: "100%", marginTop: 50, marginBottom: 50 }} />
  );
}

const BlockQuote = (props: any) => {
  return (
    <div style={{ border: "1px dashed black", borderRadius: 8, paddingLeft: 8, margin: 8 }}>
      {props.children}
    </div>
  )
}

export default ({ title, date, content, attachments, pre, post, end }: IArticle & { end: string, pre: IArticleShort | null, post: IArticleShort | null }) => {
  return (
    <>
      <TitleContainer>
        <Link to={`/${end}`}>{"<"}</Link>
        <div>{title}</div>
        <div>{date}</div>
      </TitleContainer>

      <ReactMarkdown source={content}
        renderers={{
          code: Code,
          inlineCode: InlineCode,
          image: Image,
          blockquote: BlockQuote
        }}
      />

      {attachments.map((v, i) => (
        <Attachment {...v} key={i} />
      ))}

      <div style={{ height: 50 }} />


      <Sp />
      {post && <ArticleLink title={"▲ 다음글 | " + post.title} index={post.index} end={end} />}
      {pre && post && <Sp2 />}
      {pre && <ArticleLink title={"▼ 이전글 | " + pre.title} index={pre.index} end={end} />}
      <Sp />
    </>
  );
}

const ArticleLink = ({ title, index, end }: IArticleShort & { end: string }) => {
  return (
    <ArticleStyle to={`/${end}/${index}`}>
      <div>{title}</div>
    </ArticleStyle>
  )
}

const ArticleStyle = styled(Link)`
  padding: 10px 0;
  font-size: 16pt;
  color: black;
  text-decoration: none;
`;

const Attachment = ({ fileName, link }: IAttachment) => {
  return (
    <AttachmentStyle>
      {fileName}
      <a href={link}>
        <img alt="download" src="/static/svgs/download_box.svg" />
      </a>
    </AttachmentStyle>
  )
}

const AttachmentStyle = styled.div`
  width: 500px;
  height: 50px;
  padding-left: 20px;
  font-size: 16pt;
  padding-right: 10px;
  margin-top: 10px;
  box-sizing: border-box;
  border: 1px solid #6d788f;

  display: flex;
  align-items: center;
  color: black;

  > a {
    margin-left: auto;
    margin-right: 0;
  }
`

const TitleContainer = styled.div`
  padding-bottom: 50px;
  margin-bottom: 50px;
  border-bottom: 1px solid #6d788f;

  > a:nth-child(1) {
    color: black;
    text-decoration: none;
    font-size: 24pt;
  }

  > div:nth-child(2) {
    font-size: 24pt;
    margin-bottom: 10px;
    text-align: center;
    margin-top: calc(-24pt - 10px);
  }

  > div:nth-child(3) {
    font-size: 14pt;
    color: #888;
    text-align: center;
  }
`;

const Sp = styled.div`
  height: 3px;
  background: #6d788f;
`;

const Sp2 = styled.div`
  height: 1px;
  background: #6d788f;
`;