import React from 'react';
import styled from "styled-components";
import { Link } from "react-router-dom";
import { IArticleShort } from "./articleView";


export default ({ index, title, end }: IArticleShort & { end: string }) => {
  return (
    <ArticleContainer to={`/${end}/${index}`}>
      <div>{index}</div>
      {title}
    </ArticleContainer>
  );
}

const ArticleContainer = styled(Link)`
  height: 50px;
  margin-bottom: 20px;
  border: 1px solid #6D788F;
  font-size: 16pt;
  display: flex;
  align-items: center;
  color: black;
  text-decoration: none;

  @media only screen and (min-width: 1280px) {
    &:hover {
      background: #f0f1f4;
    }
  }

  > div {
    padding: 0 20px;
    color: #D5D5D5;
  }

  &::after {
    content: ">";
    margin-left: auto;
    margin-right: 20px;
  }
`;