import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import api from '../api';
import { Link } from 'react-router-dom';

interface IList {
  title: string;
  index: number;
}

const ListBox: React.FC<{
  endpoint: string
  link: string
}> = ({ endpoint, link }) => {
  const [list, setList] = useState<IList[]>([]);
  useEffect(() => {
    api.get("/v1/" + endpoint)
      .then(({ data }) => setList(data));
  }, [endpoint])
  return (
    <Box>
      {list.map((v, i) => (
        <li key={i}>
          <Link to={`/${link}/${v.index}`}>
            {v.title}
          </Link>
        </li>
      ))}
    </Box>
  );
}

const Box = styled.ul`
  list-style: none;
  box-sizing: border-box;
  margin: 0;
  padding: 0;

  & > li {
    padding: 10px 30px;
    border-bottom: 1px solid #d9d9d9;
    font-size: 18pt;
    margin: 0 calc(50% - 50vw);

    & > a {
      color: black;
      text-decoration: none;
    }

    @media only screen and (min-width: 1280px) {
      font-size: 14pt;
      margin: 0;
      padding: 0;
      border: 0;
    }
  }

  @media only screen and (min-width: 1280px) {
    padding: 30px 50px;
    border: 1px solid #6d788f;
    border-radius: 5px;
    list-style: square;

    width: 420px;
    margin-left: 40px;
  }

  @media only screen and (min-width: 1620px) {
    flex-grow: 1;
    margin-left: 35px;
  }
`;

export default ListBox;